div.add-subjects-container {
    display: flex;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-47%, -50%);
    width: 100%;
}


form.add-subjects-form {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 8px;
    align-content: center;
    justify-content: center;
    width: 40%;
    padding: 10%;
    background-color: #9c99a8;
    border-radius:10px ;
}

form.add-subjects-form input {
    padding: 3.8%;
    width: 160%;
    margin-top: 5%;
    border: none;
    outline: none;
    border-radius: 10px;
    font-weight: bolder;
    font-size: 17px;
    font-family: var(--mainFont);
   
}


form.add-subjects-form select {
    padding: 3.8%;
    width: 140%;
    margin-top: 10%;
    border-radius: 10px;
    cursor: pointer;
    text-align: center;
    font-family: var(--mainFont);
    font-size: 1.6dvw;
}


form.add-subjects-form button {
    padding: 0%;
    width: 90%;
    height: 40px;
    border-radius: 8px;
    border: none;
    outline: none;
    transform: translateY(100%);
    font-size: 1.8dvw;
    font-weight: bolder;
    font-family: var(--mainFont);
    transition: linear .3s all;
    &:hover {
        background-color: black;
        color: white;
    }
}
