button.whatsapp-contact, button.My-Resume {
    outline: none;
    border: none;
    box-shadow: 1px 1px 0px rgba(136, 136, 136, 0.692);
    padding: 14px;
    border-radius: 15px;
    color: #fff;
    font-weight: bolder;
    font-size: 1.1rem;
    margin: 20px auto;
    display: flex;
    gap: 10px;
    background-color: #519adf;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: ease-in-out all .5s;
    flex-wrap: wrap;
    align-content: center;
   
}


button.whatsapp-contact:hover, button.My-Resume:hover {
    background: #222222;
    color: #FFF;
}

.testimonials {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.testimonial-card {
    display: flex;
    align-items: center;
    flex-direction: column;
}