

:root {
    --primary-color: #0d5599;
    --secondary-color: #fff;
    --background-color: #1C1C1C;
    --hover-color: #333333;
    --header-color: #444444;
    --font-family: var(--mainFont);
}


.container {
    width: 100vw;
    display: grid;
    grid-template-columns: 1.8fr 1.2fr;
    gap: 1rem;
    padding: 1rem;
    box-sizing: border-box;
}

@media screen and (max-width: 990px) {
    .container {
        grid-template-columns: 1fr;
    }
}

.course-header {
    grid-column: 1 / -1;
    padding: 1.5rem;
    background-color: var(--header-color);
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    text-align: center;
    margin-bottom: 1rem;
}

.course-header h1 {
    color: var(--primary-color);
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
}

.course-meta {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
    font-size: 1.1rem;
    color: var(--secondary-color);
    margin-top: 1rem;
}

.course-meta p {
    margin: 0;
}

.main-video,
.video-playlist {
    background-color: var(--header-color);
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    padding: 1rem;
}

.main-video {
    position: relative;
    height: 35rem;
}

.main-video .title {
    margin-top: 1rem;
    color: var(--primary-color);
    text-align: center;
    font-weight: bold;
    font-size: 1.5rem;
}

.main-video iframe {
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 10px 10px 0 0;
}

.video-playlist {
    margin-top: 1rem;
}

.video-playlist .title {
    padding-bottom: .33rem;
    font-size: 1.2rem;
    color: var(--secondary-color);
    border-bottom: 2px solid var(--primary-color);
    margin-bottom: .45rem;
    margin-left: 1rem;
}

.video-playlist>p {
    color: var(--secondary-color);
    margin-bottom: 1rem;
}

.video-playlist .videos {
    height: calc(100% - 4rem);
    overflow-y: auto;
    background-color: var(--background-color);
    padding: 1rem;
    border-radius: 10px;
}

.video-playlist .videos::-webkit-scrollbar {
    width: 0.6rem;
    background-color: var(--background-color);
}

.video-playlist .videos::-webkit-scrollbar-thumb {
    background-color: var(--primary-color);
    border-radius: 3px;
}

.video-playlist .videos .video {
    display: flex;
    align-items: center;
    padding: 0.5rem 1rem;
    margin-bottom: 0.5rem;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;
    background-color: #333;
}

.video-playlist .videos .video:hover {
    background-color: var(--hover-color);
}

.video-playlist .videos .video.active {
    background-color: var(--primary-color);
    color: var(--background-color);
}

.video img {
    width: 1.5rem;
    height: 1.5rem;
    filter: invert(100%);
    margin-right: 1rem;
}

.video-playlist .videos .video.active img {
    filter: invert(100%) sepia(100%) saturate(2000%) hue-rotate(360deg);
}

.video p {
    margin: 0;
}

.video h3 {
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 500;
    font-size: 1rem;
}

.video .time {
    font-size: 0.9rem;
    color: #ccc;
}

.video .desc {
    display: none;
    font-size: 0.8rem;
    color: #bbb;
}

.video:hover .desc {
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 0.5rem;
    background-color: #333;
    border-radius: 0 0 0.5rem 0.5rem;
    z-index: 1;
}

.course-footer {
    grid-column: 1 / -1;
    background-color: var(--header-color);
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    margin-top: 1rem;
    text-align: center;
}

.course-footer h3 {
    color: var(--primary-color);
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.course-footer p {
    color: var(--secondary-color);
    margin: 0.5rem 0;
}