:root {
    --main-color: #519adf;
}

.about-container {
    font-family: var(--mainFont);
    padding: 20px;
    background-color: #f9f9f9;
    color: #333;
}

.intro,
.offers,
.admin,
.developer,
.testimonials {
    margin-bottom: 40px;
}

h1.about-page,
h2.about-page,
h3.about-page,
h4.about-page {
    color: var(--main-color);
    text-align: center;
}

.intro p,
.admin-card p,
.developer-card p,
.developer-contact p,
.testimonial-card p {
    font-size: 18px;
    color: #666;
    text-align: center;
}

.cards {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

.card,
.testimonial-card,
.admin-card,
.developer-card {
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    flex: 1 1 calc(30% - 20px);
    box-sizing: border-box;
    transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover,
.testimonial-card:hover,
.admin-card:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.card h3,
.testimonial-card h4,
.admin-card h3,
.developer-card h3 {
    margin-top: 0;
}

.developer-card {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.developer-card img,
.admin-card img {
    border-radius: 50%;
    margin-bottom: 20px;
}

.contact-form {
    width: 100%;
    margin-top: 20px;
}

.contact-form label {
    display: block;
    margin-bottom: 10px;
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.contact-form button {
    background-color: var(--main-color);
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}




.admin-card {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.contact-form button:hover {
    background-color: #d95c00;
}

.developer-contact p {
    font-size: 16px;
    color: #333;
    text-align: center;
}

.test section.intro {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.testimonials {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.testimonial-card {
    display: flex;
    align-items: center;
    flex-direction: column;
}

section.intro {
    display: flex;
    align-items: center;
    flex-direction: column;
}

@media (max-width: 768px) {
    .cards {
        flex-direction: column;
    }
}

@media (max-width: 1024px) {
    .cards {
        flex-direction: column;
    }
}