/* Container for the exams list */
.exams-container {
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
    display: grid;
    flex-direction: column;
    align-items:center;
    justify-content: space-evenly;
}

/* Header styles */
.exams-title {
    text-align: center;
    font-size: 2.5rem;
    color: #519adf; /* Main color */
    margin-bottom: 30px;
}

/* Exam list styles */
.exams-list {
    list-style-type: none; /* Remove default list styling */
    padding: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    display: grid;
    place-items: center;
    place-content: center;
    grid-template-columns: repeat(2, 2fr);
    gap: 30px;
}

/* Exam item styles */
.exam-item {
    background-color: white; /* Card background */
    border: 1px solid #e1e1e1; /* Light border for card */
    border-radius: 8px;
    margin-bottom: 20px;
    display: grid;
    justify-content: space-evenly;
    align-items: center;
    padding: 15px;
    transition: transform 0.2s, box-shadow 0.2s; /* Animation effects */
}

/* Hover effect for each exam item */
.exam-item:hover {
    transform: translateY(-5px); /* Lift effect */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Shadow on hover */
}

/* Title styles */
.exam-title {
    font-size: 1.5rem;
    color: #ffebeb; /* Dark text for contrast */
    margin: 0;
   width: 100%;
}

/* Button styles */
.exam-button {
    background-color: #519adf; /* Button background */
    color: white; /* Button text color */
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 1rem;
    width: 100%;
    transition: background-color 0.3s; /* Transition effect */
}

/* Button hover effect */
.exam-button:hover {
    background-color: #417bbf; /* Darker shade on hover */
}

/* No exams message styles */
.no-exams-message {
    text-align: center;
    font-size: 1.2rem;
    color: #999; /* Grey color for message */
}

/* Responsive design */
@media (max-width: 768px) {
    .exams-title {
        font-size: 2rem; /* Reduce font size for mobile */
    }
    .exam-item {
        padding: 10px; /* Reduce padding on smaller screens */
    }
    .exam-button {
        width: 100%; /* Full-width button on small screens */
        padding: 15px; /* Increase button padding */
    }

    .exams-list {
       flex-direction: column;
       grid-template-columns: repeat(1, 1fr);
    }

    
    
}
