/* CSS for the table */
.messages-table {
  width: 70%;
  max-width: 70%; /* Restrict the table to 70% of the screen width */
  margin: 90px auto; /* Center the table horizontally */
  border-collapse: separate;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.messages-heads {
  background-color: #519adf;
  color: white;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 18px;
}

.messages-rows {
  transition: background-color 0.3s ease;
}

.messages-rows:nth-child(even) {
  background-color: #f9f9f9;
}

.id-field {
  text-align: center;
  font-weight: bold;
}

td {
  padding: 15px;
  text-align: center;
  border-bottom: 1px solid #dddddd;
  font-size: 14px;
  color: #333;
}

/* Buttons */
button.messageeee, button.updateed, button.editOffer, button.validMessage {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  font-size: 14px;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

button.messageeee img, button.updateed img, button.editOffer img, button.validMessage img {
  vertical-align: middle;
  margin-left: 5px;
}

.validMessage {
  background-color: #28a745;
  color: white;
}

.editOffer {
  background-color: #ff7200;
  color: white;
}

button:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transform: translateY(-2px);
}

button:active {
  transform: translateY(0);
}

/* Subject Form */
.subject-form {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
  padding: 30px;
  margin: 50px auto;
  max-width: 600px;
  text-align: center;
}

.subject-form h3 {
  color: #ff7200;
  margin-bottom: 20px;
  font-size: 24px;
}

.subject-form label {
  font-size: 16px;
  font-weight: bold;
  color: #333;
}

.subject-form select {
  width: 100%;
  padding: 10px;
  margin-top: 15px;
  border-radius: 4px;
  border: 1px solid #ddd;
  font-size: 14px;
}

.subject-form button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
}

button.block-user {
  background-color: #dc3545;
  padding: 15px;
  border: none;
  outline: none;
  border-radius: 12px;
}

.subject-form button[type="submit"] {
  background-color: #28a745;
  color: white;
}

.subject-form button[type="button"] {
  background-color: #dc3545;
  color: white;
}

.subject-form button[type="button"]:hover {
  background-color: #e74c3c;
}

/* Responsive Design */
@media (max-width: 768px) {
  .messages-table {
    width: 70% !important;
    max-width: 70%; /* Ensure the table width remains at 70% */
    font-size: 12px;
  }

  td {
    padding: 10px;
  }

  button {
    font-size: 12px;
    padding: 8px 16px;
  }

  .subject-form {
    padding: 20px;
  }

  .subject-form h3 {
    font-size: 20px;
  }

  .subject-form select {
    padding: 8px;
  }

  .subject-form button {
    padding: 8px 16px;
  }
}

@media (max-width: 480px) {
  .messages-table {
    width: 70% !important;
    max-width: 70% !important; /* Ensure the table width remains at 70% */
  }

  td, .messages-heads td {
    font-size: 10px;
  }

  .subject-form h3 {
    font-size: 18px;
  }

  .subject-form select {
    font-size: 12px;
  }
}
