article.admin-settings 
{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    width: 100%;
    margin-left: 8%;
    position: absolute;
    top: 20%;
}


.loader {
    padding: 13px;
    color: white;
}

button.unblock-user {
    background-color: #4f993c;
  padding: 15px;
  border: none;
  outline: none;
  border-radius: 12px;
}


article.admin-settings div 
{
    width: calc(80% / 2);
    margin: 2%;
    background-color: #519adf;
    align-items: center;
    align-content: center;
    display: flex;
    gap: 20px;
    border-radius: 10px;
    cursor: pointer;
    transition: ease-in-out all .3s;
    border: 1px solid #fff;
}

article.admin-settings div p {
    font-weight: 350;
    letter-spacing: 1px;
    font-family: var(--mainFont);
    font-size: 27px;
    color: white;
}

article.admin-settings div img {
    object-fit:cover;
    object-position: center;
    filter: drop-shadow(rgb(16, 16, 16) 0.5rem 0.5rem 1rem);
}


article.admin-settings div:not(#first-layout) img {
    border-radius: 50%;
}

article.admin-settings div:not(#first-layout):hover{
    transform: scale(1);
}

article.admin-settings div:hover{
    transform: scale(1.035);
}


article.admin-settings div:not(#first-layout) input {
    padding: 10px;
    width: 60%;
    border-radius: 10px;
    border: none;
    outline: none;
}

article.admin-settings div:not(#first-layout) button:not(.updateed, .messageeee){
    padding: 8px;
    border-radius: 8px;
    border: none;
    outline: none;
    font-weight: bolder;
    font-family: var(--mainFont);
    background-color: rgb(19, 163, 163);
    color: white;

    &:hover {
        transform: scale(1.044);
    } 
}




div.vid-upload-ads button {
    color: white;
    background-color: #519adf;
}







div.show-after-click {
    display: none;
    text-align: center;
}



div.return {
    display: flex;
    align-content: flex-start;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
    position: absolute;
    top: 11%;
    width: 100%;
    left: 12%;
}

div.return button {
    padding: 12px;
    width: 15%;
    border: none;
    outline: none;
    border-radius: 10px;
    background-color: #519adf;
    color: white;
    font-weight: bolder;
    font-family: var(--mainFont);
    font-size: 22px;
    letter-spacing: 1px;
    transition: ease-in-out .3s all;
    z-index: 1000;

    &:hover{
        background-color: white;
        color: black;
    }
}


article.admin-settings form {
    display: flex;
    align-items: center;
    align-content: center;
    flex-direction: column;
    justify-content: center;
    gap: 3px;
    width: 90%;
    transform: translateY(-15%);
}

article.admin-settings form h1 {
    transform: translateY(-20%);
    font-size: 27px;
    padding: 1% 16%;
    background-color: #519adf;
    border-radius: 5px;
}

article.admin-settings form input{
    padding: 12px;
    width: 40%;
    outline: none;
    border: none;
    border-radius: 7px;
    font-size: 17px;

    &::placeholder {
        font-family: var(--mainFont);
        font-size: 15px;
        letter-spacing: 2px;
    }
}

article.admin-settings form button {
    padding: 10px;
    transform: translateY(80%);
    width: 40%;
    outline: none;
    border: none;
    border-radius: 10px;
    font-weight: bolder;
    font-size: 19px;
    align-items: center;
    align-content: center;
    align-self: center;
    font-family: var(--mainFont);
    background-color: #519adf;
    color: black;
    transition: ease-in-out .3s all;
    &:hover {
        background-color: white;
        color: black;
    }

}


article.admin-settings form select{
    width: 40%;
    padding: 12px;
}





article a {
  width: 80%;
}

div.online-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
}

div.online-content div {
    width: 80%;
    &:hover {
        transform: scale(1.066) !important;
    }
}

div.offline-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-content: flex-end;
    align-items: flex-end;
}

div.offline-content div {
    width: 80%;
    &:hover {
        transform: scale(1.066) !important;
    }
}



div.setup-content  {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex: .5;
    flex-wrap: wrap;

}

div.setup-content div {
    width: 80%;
    display: flex !important;
    align-items: center !important;
    align-content: center !important;
    justify-content: center !important;
    flex-wrap: wrap;
    margin-left: 10%;
    &:hover {
        transform: scale(1.066) !important;
    }
}


article#content-management-id {
    margin-left: 30%;
    align-items: center;
}


div.vid-upload-ads {
    display: grid !important;
    position: absolute;
    left: 25%;
    top: 50%;

}


div.vid-upload-ads button {
    transition: ease-in-out all .3s;
    font-size: 2vmax;
}


div.vid-upload-ads h1 {
    background-color: black;
    color: #fff;
}

div.vid-upload-ads button:hover {
    transform: scale(1) !important;
    background-color: black !important;
}

input::-webkit-file-upload-button {
    border: none;
    outline: none;
    border-radius: 10px;
    padding: 10px;
    font-family: var(--mainFont);
    cursor: pointer;
    transition: ease-in-out .4s all;
   
}

input::-webkit-file-upload-button:hover {
    background-color: black !important;
    color: #fff;
}


@media (max-width: 710px) {

    article.admin-settings{
        flex-direction: column;
        top: 0;
    }


    article.admin-settings div p {
        font-size: 18px;
    }

    article.admin-settings div img {
        height: 70px;
    }
}





