/* Global Styles */



/* Container for Subjects */
.grand-subjects-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 10%;
  top: 10%;
  min-height: 100vh;
  padding: 2rem;
}

.subjects-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  width: 100%;
  max-width: 1200px;
  padding: 2rem;
  background-color: #fff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
}

/* Individual Subject Card */
.subjects-container div {
  background-color: #519adf;
  padding: 1.5rem;
  border-radius: 12px;
  text-align: center;
  color: #fff;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.subjects-container div:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.subjects-container h1 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: white;
}

.subjects-container p {
  font-size: 1rem;
  margin-bottom: 1.5rem;
  color: white ;
}

/* Button Container */
.subjects-container button {
  display: inline-block;
  padding: 0.6rem 1.2rem;
  margin-top: 1rem;
  border: none;
  border-radius: 50px;
  background-color: #2c2b2b;
  color: #FFF;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.subjects-container button:hover {
  background-color: #66bee7;
  color: #fff;
  transform: scale(1.05);
}


button {
  cursor: pointer;
}

/* Setup Form */
.setup-subject {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
}

.setup-form {
  background-color: #fff;
  padding: 2rem;
  border-radius: 12px;
  width: 100%;
  max-width: 500px;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.setup-form select,
.setup-form input {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #ff7200;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.setup-form input:focus,
.setup-form select:focus {
  border-color: #e66200;
  outline: none;
}

.setup-form button {
  width: 100%;
  padding: 0.75rem;
  border: none;
  border-radius: 50px;
  background-color: #ff7200;
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

.setup-form button:hover {
  background-color: #e66200;
}

.setup-form button:last-child {
  background-color: #333;
}

.setup-form button:last-child:hover {
  background-color: #555;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .subjects-container {
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
      padding: 1.5rem;
  }

  .subjects-container h1 {
      font-size: 1.25rem;
  }

  .subjects-container p {
      font-size: 0.9rem;
  }
}

@media screen and (max-width: 480px) {
  .subjects-container {
      grid-template-columns: 1fr;
      padding: 1rem;
  }

  .subjects-container div {
      padding: 1rem;
  }

  .subjects-container h1 {
      font-size: 1.1rem;
  }

  .subjects-container p {
      font-size: 0.85rem;
  }

  .setup-form {
      padding: 1.5rem;
  }

  .setup-form button {
      font-size: 0.9rem;
  }
}
