/* Container for the dashboard */
div.dashboard-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    position: absolute;
    top: 10%;
    left: 10%;
    height: 100vh;
    width: 80%;
    gap: 2%;
    text-align: center;
    padding: 2%;
}

/* Dashboard item style */
div.dashboard-container div {
    width: 22%;
    height: 20vh;
    background-color: rgb(84, 85, 85);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bolder;
    font-size: 2vw;
    border-radius: 10px;
    letter-spacing: 2px;
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Hover effect */
div.dashboard-container div:hover {
    transform: translateY(-10px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

/* Dashboard title */
h1.dashboard {
    position: absolute;
    top: 0%;
    color: #519adf;
    left: 50%;
    transform: translateX(-50%);
    font-size: 2.5vw;
    text-align: center;
}

/* Dashboard subtitle */
h3.interface {
    color: #519adf;
    font-size: 1.9vw;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

/* Media queries for responsiveness */

/* Extra small devices (phones, 600px and down) */
@media (max-width: 600px) {
    div.dashboard-container {
        left: 2%;
        width: 96%;
        height: auto;
    }

    div.dashboard-container div {
        width: 100%;
        height: 12vh;
        font-size: 5vw;
    }

    h1.dashboard {
        font-size: 6vw;
    }

    h3.interface {
        font-size: 4.5vw;
    }
}

/* Small devices (tablets, 600px to 768px) */
@media (min-width: 600px) and (max-width: 768px) {
    div.dashboard-container {
        left: 5%;
        width: 90%;
    }

    div.dashboard-container div {
        width: 48%;
        height: 15vh;
        font-size: 3.5vw;
    }

    h1.dashboard {
        font-size: 5vw;
    }

    h3.interface {
        font-size: 3.5vw;
    }
}

/* Medium devices (landscape tablets and small desktops, 768px to 992px) */
@media (min-width: 768px) and (max-width: 992px) {
    div.dashboard-container {
        left: 7%;
        width: 86%;
    }

    div.dashboard-container div {
        width: 32%;
        height: 18vh;
        font-size: 2.8vw;
    }

    h1.dashboard {
        font-size: 4vw;
    }

    h3.interface {
        font-size: 3vw;
    }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
    div.dashboard-container {
        left: 10%;
        width: 80%;
    }

    div.dashboard-container div {
        width: 22%;
        height: 20vh;
        font-size: 2vw;
    }

    h1.dashboard {
        font-size: 2.5vw;
    }

    h3.interface {
        font-size: 2vw;
    }
}
