main.admin-auth {
    display: grid;
    place-items: center;
    height: 100vh;
    text-align: center;
    
}



main.admin-auth form 
{
    display: flex;
    flex-direction: column;
    gap: 17px;
}

main.admin-auth form h1 
{
    font-family: var(--mainFont);
    font-weight: bolder;
    color:#d66d11;
    margin-top: -25%;
    transform: translateY(-20%);
}


main.admin-auth form input 
{
    padding: 20px;
    border: 1px solid rgb(214, 109, 17);
    border-radius: 10px;
    outline: none;
    font-weight: bolder;
    font-size: 20px;
}

main.admin-auth form input:nth-of-type(1) {
    transform: translateY(100%);
} 

main.admin-auth form input:nth-of-type(2) {
    transform: translateY(120%);
} 
main.admin-auth form select 
{
    padding: 15px;
    width: 60%;
    margin-top: 14%;
    background-color: #000;
    color: rgb(214, 109, 17);
    border-radius: 12px 14px;
    font-weight: bolder;
    text-align: center;
    font-size: 18px;
    cursor: pointer;
    transition: ease-in-out all .5s;
    transform: translateY(72%);

    &:hover {
        background-color: #080808;
    }
}

main.admin-auth form select option {
    border-radius: 15px 10px;
    background-color: white;
    color: #000;
    font-size: 20px;
}



main.admin-auth form button {
    padding: 15px;
    border-radius: 12px;
    border: 2px solid #000;
    background-color: rgb(214, 109, 17) ;
    font-weight: bolder;
    font-size: 20px;
    transform: translateY(120%);
}


main.admin-auth form button:hover 
{
    background-color: #000;
    color: white;
    border: 2px solid rgb(214, 109, 17);
}











