div.grand-parent-container {
    display: flex;
    height: 100vh;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 100%;
}



div.thank-you-parent {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;

}

div.thank-you-parent div.thank-you-parent-container {
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
    align-items: center;
    align-content: center;
}


div.thank-you-parent div.thank-you-parent-container img {
    object-fit: cover;
    animation-name: zoomInOut;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-delay: .6s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: backwards;
    animation-direction: alternate;
    transition: linear all .3s;

}





div.thank-you-parent div.thank-you-parent-container h1 {
    color: #ffc727;
    font-family: var(--mainFont);
    font-weight: bolder;
    font-size: 4svw;
}


div.thank-you-parent div.thank-you-parent-container p {
    padding: 10px;
    background-color: #000 !important;
    border-radius: 10px;
    font-family: var(--mainFont);
    font-weight: bolder;
    font-style: italic;
    color: #ffc727;
}


div.thank-you-parent div.thank-you-parent-container button {
    border: none;
    outline: none;
    background-color: steelblue;
    color: #000;
    font-weight: bolder;
    font-family: var(--mainFont);
    font-size: 1.5svw;
    padding: 10px;
    width: 40%;
    border-radius: 15px;
    transition: ease-in-out all 300ms;

    &:hover {
        background-color: #ffc727;
        transform: scale(1.044);
    }
}


@keyframes zoomInOut {

    0%,
    100% {
        transform: scale(.9);
    }

    50%,
    70% {
        transform: scale(1);
    }
}