/* Panel Styles */
aside.panel {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    padding: 20px;
    background: #519adf;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
}

aside.panel button {
    flex: 1;
    margin: 10px;
    padding: 15px;
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    background: #252525;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
}

aside.panel button:hover {
    background: #464545;
}

aside.panel button:active {
    background: #464545;
}

/* Members Data Styles */
.members-data {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin: 20px;
}

/* Common Card Styles */
.teachers,
.assistants,
.technicals {
    display: none;
    flex-direction: column;
    width: 100%;
}

.teacher-column,
.technicals-column,
.assistants-column {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

.teacher-column:hover,
.technicals-column:hover,
.assistants-column:hover {
    transform: scale(1.05);
}

.teacher-column h1,
.technicals-column h1,
.assistants-column h1 {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 10px;
}

.teacher-column h2,
.technicals-column h2,
.assistants-column h2 {
    font-size: 1.2rem;
    color: #519adf;
    margin-bottom: 10px;
}

.teacher-column p,
.technicals-column p,
.assistants-column p {
    font-size: 1rem;
    color: #666;
    margin-bottom: 5px;
    text-align: center;
}

/* Button Actions */
.btn-actions {
    display: flex;
    gap: 10px;
    margin-top: 10px;
}

.btn-actions button {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease;
}

.btn-actions button:nth-of-type(1) {
    background: #ff7200;
    color: #fff;
}

.btn-actions button:nth-of-type(2) {
    background: #ffb347;
    color: #fff;
}

.btn-actions button:hover:nth-of-type(1) {
    background: #ff4500;
}

.btn-actions button:hover:nth-of-type(2) {
    background: #ffaa00;
}

/* Add User Button */
.add-user-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.add-user-btn {
    background: #519adf;
    color: #fff;
    padding: 15px 30px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease;
}

.add-user-btn:hover {
    background: #5ad86a;
}

/* Overlay Form */
.add-user-form {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.form-user {
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.form-user input,
.form-user select {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
}

.form-user button {
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s ease;
}

.add-user-btn-form {
    background: #4CAF50;
    color: #fff;
}

.add-user-btn-form:hover {
    background: #45a049;
}

.cancel-user-btn-form {
    background: #dd4444;
    color: #fff;
}

.cancel-user-btn-form:hover {
    background: #bb2f2a;
}

.update-user-btn-form {
    background: #cfcf1d;
    color: #000;
    display: none;
}

.update-user-btn-form:hover {
    background: rgba(207, 207, 29, 0.8);
}

/* Media Queries */
@media (max-width: 768px) {
    .panel {
        flex-direction: column;
        padding: 10px;
    }

    .panel button {
        width: 90%;
    }

    .members-data {
        flex-direction: column;
        align-items: center;
    }

    .teacher-column,
    .technicals-column,
    .assistants-column {
        width: 100%;
    }
}

.all-members-show {

    position: absolute;
    top: 20%;
    left: 22%;
    width: 62%;

}