section.owner-container {
    /* background-image: url("../images/sidebar-imgs/background.png") !important; */
    background-size: cover;
    width: 100%;
    /* height: 100vh; */
    background-position: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

div.owner-sidebar {
    width: 80px;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    padding: 20px 10px;
    background-color: #519adf;
    backdrop-filter: blur(5px);
    display: flex;
    z-index: 9999;
    justify-content: space-between;
    flex-direction: column;
    transition: width .5s;
    color: white;
    font-weight: bolder;
}

div.user {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60px;
    font-size: 12px;
    padding: 10px;
    border-radius: 8px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
}

div.user div {
    display: none;
}

div.user h2 {
    font-size: 15px;
    font-weight: 600;
    white-space: nowrap;
}

.user-img {
    width: 40px;
    border-radius: 50%;
    margin: auto;
}

.star-img {
    width: 20px;
    display: none;
}

div.user h2+p {
    font-weight: bold;
}

ul.owner-dash-ul {
    list-style: none;
    padding: 0 15px;
    
}

ul.owner-dash-ul li {
    margin: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: ease-in-out all .3s;
    border-bottom: 1px solid #000000;
}

ul.owner-dash-ul li img {
    width: 30px;
    margin-right: 0px;
}

ul.owner-dash-ul li p {
    white-space: nowrap;
    font-weight: bolder;
    color: white;

    display: none;
}

div.owner-sidebar:hover {
    width: 250px;
}

div.owner-sidebar:hover div.user div {
    display: block;
}

div.owner-sidebar:hover div.user {
    width: 100%;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(5px);
}

div.owner-sidebar:hover .star-img {
    display: block;
}

div.owner-sidebar:hover .user-img {
    margin: 0;
}

div.owner-sidebar:hover ul li p {
    display: block;
}

div.owner-sidebar:hover ul li img {
    margin-right: 10px;
}

div.owner-sidebar:hover ul li {
    justify-content: flex-start;
}

ul.owner-dash-ul li:hover {
    transform: scale(1.022);
}


@media (max-width: 600px) {
    div.owner-sidebar {
        width: 60px;
    }

    div.owner-sidebar:hover {
        width: 200px;
    }

    ul.owner-dash-ul li {
        margin: 20px;
    }

    ul.owner-dash-ul li img {
        width: 25px;
    }

    div.user {
        width: 50px;
    }

    .user-img {
        width: 30px;
    }
}


@media (min-width: 600px) and (max-width: 768px) {
    div.owner-sidebar {
        width: 70px;
    }

    div.owner-sidebar:hover {
        width: 220px;
    }

    ul.owner-dash-ul li {
        margin: 25px;
    }

    ul.owner-dash-ul li img {
        width: 28px;
    }

    div.user {
        width: 55px;
    }

    .user-img {
        width: 35px;
    }
}


@media (min-width: 768px) and (max-width: 992px) {
    div.owner-sidebar {
        width: 90px;
    }

    div.owner-sidebar:hover {
        width: 240px;
    }

    ul.owner-dash-ul li {
        margin: 28px;
    }

    ul.owner-dash-ul li img {
        width: 30px;
    }

    div.user {
        width: 60px;
    }

    .user-img {
        width: 40px;
    }
}


@media (min-width: 992px) {
    div.owner-sidebar {
        width: 110px;
    }

    div.owner-sidebar:hover {
        width: 250px;
    }

    ul.owner-dash-ul li {
        margin: 30px;
    }

    ul.owner-dash-ul li img {
        width: 30px;
    }

    div.user {
        width: 60px;
    }

    .user-img {
        width: 40px;
    }
} 



