/* Parent container styles */
div.parent {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  padding: 10px;
  height: fit-content;
}

/* Title styles */
h1.parent {
  border-radius: 15px;
  text-align: center;
  color: #0d5599;
  padding: 5px;
  background-color: #1f1e1eca;
  background-size: contain;
  transform: translateY(120%);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

/* Form container styles */
form.parent {
  width: 80%;
  max-width: 600px;
  margin: 10% auto;
  padding: 20px;
  border-radius: 10px;
  z-index: 99999999999999;
  box-shadow: 4px 5px 13px 3px #d17e1233;
  border: 1px solid #cccccc;
  background-color: #ffffff;
  position: relative;
}

/* Form group styles */
.form-group {
  margin-bottom: 10px;
}

/* Label styles */
label.parent {
  display: block;
  font-weight: bold;
}

/* Input and select styles */
input.parent,
select.parent {
  width: 100%;
  border: 1px solid #5e5e5e;
  padding: 5px;
  font-family: var(--mainFont);
  font-weight: bolder;
  color: #5e5e5e;
}

/* Button styles */
button.btn-form {
  display: block;
  width: 100%;
  padding: 6px;
  border: none;
  background-color: #0d5599;
  color: #ffffff;
  font-weight: bolder;
  cursor: pointer;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 24px;
}

/* Footer styles */
.main .footer {
  z-index: 1;
  --footer-background: #0d5599;
  display: grid;
  position: relative;
  min-height: 12rem;
}

.main .footer .bubbles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  filter: url("#blob");
}

.main .footer .bubbles .bubble {
  position: absolute;
  left: var(--position, 50%);
  background: var(--footer-background);
  border-radius: 100%;
  animation: bubble-size var(--time, 4s) ease-in infinite var(--delay, 0s),
    bubble-move var(--time, 4s) ease-in infinite var(--delay, 0s);
  transform: translate(-50%, 100%);
}

.main .footer a,
.main .footer p {
  color: #F5F7FA;
  text-decoration: none;
}

.main .footer b {
  color: white;
}

.main .footer p {
  margin: 0;
  font-size: .75rem;
}

.main .footer>div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main .footer>div>div {
  margin: 0.25rem 0;
}

.main .footer>div>div>* {
  margin-right: .5rem;
}

.main .footer .image {
  align-self: center;
  width: 4rem;
  height: 4rem;
  margin: 0.25rem 0;
  background-size: cover;
  background-position: center;
}

/* Bubble animations */
@keyframes bubble-size {

  0%,
  75% {
    width: var(--size, 4rem);
    height: var(--size, 4rem);
  }

  100% {
    width: 0rem;
    height: 0rem;
  }
}

@keyframes bubble-move {
  0% {
    bottom: -4rem;
  }

  100% {
    bottom: var(--distance, 10rem);
  }
}

/* Responsive styles */
@media (max-width: 600px) {
  form.parent {
    width: 90%;
    z-index: 9999999999999;
  }
}

@media (max-height: 641px) {
  form.parent {
    transform: scale(.9, .9) translateY(-27%);
    z-index: 9999999999999;
  }

  h1.parent {
    transform: translate(-84%, 635%) rotate(270deg);
  }
}