section.configure_inscription {
    background-color: rgb(255,255,255) !important;
    display: flex;
    flex: 1;
    height: 100vh;
    justify-content: center;
    align-items: center;
    opacity: .78;
}

section.configure_inscription div {
    height: 30%;
    width: 60%;
    background-color: rgb(139, 139, 139);
    display: flex;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
    border-radius: 10px;
    position: relative;
}

section.configure_inscription div input{
    padding: 2%;
    width: 70%;
    border: none;
    outline: none;
    border-radius: 10px;
    display: block;
    position: absolute;
    top: 38%;
    font-weight: bolder;
    font-family: var(--mainFont);
    font-size: 1.4dvw;

}

section.configure_inscription div button {
    position: absolute;
    /* top: 70%; */
    top: 19dvh ;
    padding: 2dvh;
    border: none;
    outline: none;
    border-radius: 10px;
    font-family: var(--mainFont);
    font-weight: bolder;
    font-size: 1.4dvw;
    transition: linear .3s all;
    background-color: rgb(105, 161, 50);
    color:white;
    &:hover{
        transform: scale(1.055);
        background-color: white;
        color: black;
    }

}

section.configure_inscription div h1 {
    position: absolute;
    top: 3dvh;
    font-family: var(--mainFont);
    font-weight: bolder;
    font-size: 3.5dvw;
}