/* Color Variables */
:root {
    --mainColor: #0d5599;
    --backgroundColor: #f4f4f4;
    --textColor: #333;
    --borderColor: rgba(0, 0, 0, 0.1);
    --shadowColor: rgba(0, 0, 0, 0.15);
    --hoverShadowColor: rgba(0, 0, 0, 0.3);
    --headerBackground: rgba(0, 0, 0, 0.3);
}



.auth-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

/* Header */
.header-auth {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 20px;
    background-color: var(--headerBackground);
    color: #fff;
    flex-wrap: wrap; /* Allow header items to wrap on small screens */
}

.user-info {
    display: flex;
    align-items: center;
    gap: 25px;
}

.user-icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 15px;
}

.username {
    font-size: 2rem;
    font-weight: 600;
    text-transform: capitalize;
}

.logout-button {
    background-color: var(--mainColor);
    color: #fff;
    border: none;
    border-radius: 50px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.logout-button:hover {
    background-color: #0b457a;
}

.user-details {
    margin-top: 10px;
}

.detail {
    font-size: 1.35rem;
    margin-right: 20px;
    color: #fff;
}

.detail span {
    font-weight: bolder;
}

/* Main Content */
.main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.greeting {
    margin-bottom: 30px;
    text-align: center;
}

.greeting h1 {
    font-size: 3rem;
    font-weight: 700;
    color: var(--mainColor);
}

/* Subjects Section */
.subjects {
    width: 100%;
}

.subjects h2 {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 20px;
    text-align: center;
    color: var(--mainColor);
}

.ssubjects-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.subject-item-container {
    background-color: var(--backgroundColor);
    border-radius: 10px;
    padding: 20px;
    width: 300px;
    height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    cursor: pointer;
    box-shadow: 0 4px 15px var(--shadowColor);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.subject-item-container:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 30px var(--hoverShadowColor);
}

.subject-item-container img.subj-img {
    margin-bottom: 10px;
    filter: drop-shadow(2px 4px 6px black);
    border-radius: 15px;
}

.subject-item {
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--mainColor);
    text-transform: capitalize;
}

/* Enhanced Responsive Design */

/* Medium Screens (Tablets) */
@media (max-width: 768px) {
    .username {
        font-size: 1.8rem;
    }

    .logout-button {
        padding: 8px 16px;
        font-size: 0.9rem;
    }

    .greeting h1 {
        font-size: 2.5rem;
    }

    .subject-item-container {
        width: 150px;
        height: 150px;
        padding: 15px;
    }

    .subject-item-container img {
        width: 180px;
        height: 180px;
    }

    .subject-item {
        font-size: 1.2rem;
    }
}

/* Small Screens (Phones) */
@media (max-width: 480px) {
    .header-auth {
        flex-direction: column;
        text-align: center;
    }

    .username {
        font-size: 1.5rem;
    }

    .logout-button {
        padding: 6px 12px;
        font-size: 0.8rem;
    }

    .greeting h1 {
        font-size: 2rem;
    }

    .subjects h2 {
        font-size: 2rem;
    }

    .subject-item-container {
        width: 120px;
        height: 120px;
        padding: 10px;
    }

    .subject-item-container img {
        width: 160px;
        height: 160px;
    }

    .subject-item {
        font-size: 1rem;
    }
}

/* Ultra Small Screens (Phones < 360px) */
@media (max-width: 360px) {
    .username {
        font-size: 1.3rem;
    }

    .logout-button {
        padding: 4px 10px;
        font-size: 0.7rem;
    }

    .greeting h1 {
        font-size: 1.8rem;
    }

    .subjects h2 {
        font-size: 1.8rem;
    }

    .subject-item-container {
        width: 100px;
        height: 100px;
        padding: 8px;
    }

    .subject-item-container img {
        width: 140px;
        height: 140px;
    }

    .subject-item {
        font-size: 0.9rem;
    }
}
