.container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
    gap: 20px;
}

.reservation,
.rv-prooof {
    background-color: #e2e2e291;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
}

.reservation:hover,
.rv-prooof:hover {
    transform: translateY(-5px);
}

.reservation {
    max-width: 600px;
    width: 100%;
    text-align: center;
}

.reservation h1 {
    margin-bottom: 20px;
    font-size: 28px;
    color: #0d5599;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}

.reservation input,
.reservation select {
    width: calc(100% - 40px);
    padding: 12px;
    margin: 10px 20px;
    border: 2px solid #ddd;
    border-radius: 8px;
    font-size: 16px;
    background-color: #f9f9f9;
    transition: border-color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.reservation input:focus,
.reservation select:focus {
    border-color: #0d5599;
    background-color: #ffffff;
    outline: none;
}

.reservation select {
    appearance: none;
    background-image: linear-gradient(45deg, transparent 50%, #0d5599 50%), linear-gradient(135deg, #0d5599 50%, transparent 50%);
    background-position: calc(100% - 20px) calc(1.5em + 4px), calc(100% - 15px) calc(1.5em + 4px);
    background-size: 5px 5px, 5px 5px;
    background-repeat: no-repeat;
}

.reservation select::-ms-expand {
    display: none;
}

.reservation input::placeholder,
.reservation select::placeholder {
    color: #bbb;
}

.reserver {
    width: calc(100% - 40px);
    margin: 20px 20px 0 20px;
    padding: 12px;
    background-color: #0d5599;
    border: none;
    border-radius: 8px;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}

.reserver:hover {
    background-color: #0056b3;
}

.rv-prooof {
    max-width: 450px;
    width: 100%;
    text-align: center;
}

.rv-prooof h3 {
    font-size: 24px;
    color: #0d5599;
    font-size: 35px;
}

@media (max-width: 768px) {
    .container {
        flex-direction: column;
        align-items: stretch;
    }

    .reservation,
    .rv-prooof {
        max-width: 100%;
    }
}