 @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

 main.contact-main {
     min-height: 100vh;
     width: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
     background-image: url(https://images.pexels.com/photos/325185/pexels-photo-325185.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
     background-size: cover;
     background-position: center;
     position: relative;
 }

 main.contact-main::after {
     content: "";
     position: absolute;
     top: 0;
     left: 0;
     height: 100%;
     width: 100%;
     background-color: rgba(0, 0, 0, 0.8);
 }



 section.contact-section {
     position: relative;
     z-index: 3;
     padding-top: 50px;
     padding-bottom: 50px;
 }

 .c-container {
     max-width: 1080px;
     margin-left: auto;
     margin-right: auto;
     padding-left: 20px;
     padding-right: 20px;
 }

 .section-header {
     margin-bottom: 50px;
     text-align: center;
 }

 .section-header h2 {
     color: #FFF;
     font-weight: bold;
     font-size: 3em;
     margin-bottom: 20px;
 }

 .section-header p {
     color: #FFF;
 }

 .row {
     display: flex;
     flex-wrap: wrap;
     align-items: center;
     justify-content: space-between;
 }

 .contact-info {
     width: 50%;
 }

 .contact-info-item {
     display: flex;
     margin-bottom: 30px;
 }

 .contact-info-icon {
     height: 70px;
     width: 70px;
     background-color: #fff;
     text-align: center;
     border-radius: 50%;
 }

 .contact-info-icon i {
     font-size: 30px;
     line-height: 70px;
 }

 .contact-info-content {
     margin-left: 20px;
 }

 .contact-info-content h4 {
     color: #519adf;
     font-size: 1.4em;
     margin-bottom: 5px;
 }

 .contact-info-content p {
     color: #FFF;
     font-size: 1em;
 }

 .contact-form {
     background-color: #fff;
     padding: 40px;
     width: 45%;
     padding-bottom: 20px;
     padding-top: 20px;
     color: black;
 }

 .contact-form h2 {
     font-weight: bold;
     font-size: 2em;
     margin-bottom: 10px;
     color: #333;
 }

 .contact-form .input-box {
     position: relative;
     width: 100%;
     margin-top: 10px;
 }

 .contact-form .input-box input,
 .contact-form .input-box textarea {
     width: 100%;
     padding: 5px 0;
     font-size: 16px;
     margin: 10px 0;
     border: none;
     border-bottom: 2px solid #333;
     outline: none;
     resize: none;
 }

 .contact-form .input-box span {
     position: absolute;
     left: 0;
     padding: 5px 0;
     font-size: 16px;
     margin: 10px 0;
     pointer-events: none;
     transition: 0.5s;
     color: #666;
 }

 .contact-form .input-box input~span,
 .contact-form .input-box textarea~span {
     color: #519adf;
     font-size: 12px;
     transform: translateY(-20px);
 }

 .contact-form .input-box input[type="submit"] {
     width: 100%;
     background: #519adf;
     color: #FFF;
     border: none;
     cursor: pointer;
     padding: 10px;
     font-size: 18px;
     border: 1px solid #00bcd4;
     transition: 0.5s;
 }

 .contact-form .input-box input[type="submit"]:hover {
     background: #FFF;
     color: #00bcd4;
 }

 @media (max-width: 991px) {
     section {
         padding-top: 50px;
         padding-bottom: 50px;
     }

     .row {
         flex-direction: column;
     }

     .contact-info {
         margin-bottom: 40px;
         width: 100%;
     }

     .contact-form {
         width: 100%;
     }
 }