div.title-offerss {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    flex: 1;
}

div.title-offerss div {
    background-color:#ff725e;
    /* width: 25dvh ; */
    width: clamp(19rem, 4vh, 2rem);
    padding-top: 2dvh ;
    padding-bottom: 2dvh ;
    border-radius: 15dvh;
    text-align: center;
    color: #333 ;
    box-shadow: 1px 2px 3px 4px #444;
    font-weight: bolder;
    font-family: var(--mainFont);
    font-size: clamp(0.8rem, 4vh, 2rem);
    position: absolute !important;
    top: 20dvh !important;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
  }

  main.off {
    display: flex ;
    justify-content: center ;
    align-items: center ;
    flex-wrap: wrap ;
    transform: translateY(40dvh) !important;
  }