body,
html {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    --mainColor: #519adf;
    --mainFont: "Lato", sans-serif;
    min-height: 100vh; /* Ensure the body takes the full viewport height */
    overflow-x: hidden; /* Prevent horizontal scroll, if needed */
    width: 100%; /* Ensure the body takes full width of the viewport */
    display: block; /* Make sure the body behaves as a block container */
    overflow-x: hidden;
}






/* Header Animation */
.headerAnime {
    position: relative;
    width: 100%;
    text-align: center;
    background: linear-gradient(60deg, var(--mainColor) 0%, #333 100%);
    color: white;
    font-family: var(--mainFont);
}

.logoAnime {
    width: 50px;
    fill: rgb(45, 155, 206);
    padding-right: 15px;
    font-family: "Allerta Stencil", "cursive";
    display: inline-block;
    vertical-align: middle;
}

.inner-header {
    height: auto;
    font-family: var(--mainFont);
    width: 100%;
    margin: 0;
    padding: 1rem;
    /* Added padding for better spacing */
}

.flexAnime {
    /* Flexbox for containers */
    display: flex;
    flex-direction: column;
    /* Stack items vertically for mobile */
    align-items: center;
    text-align: center;
}


.waves {
        position: relative;
        width: 100%;
        height: 26px;
        margin-bottom: -7px;
        min-height: 26px;
        max-height: 150px;
}


/* Animation */
.parallax>use {
    animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}

.parallax>use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
}

.parallax>use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
}

.parallax>use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
}

.parallax>use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
}

@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }

    100% {
        transform: translate3d(85px, 0, 0);
    }
}

/* Responsive Styles */
@media (max-width: 768px) {
    .inner-header {
        padding: 1rem;
        /* Ensure padding on mobile */
    }

    .logoAnime {
        width: 40px;
        /* Smaller logo for mobile */
        padding-right: 10px;
    }

    .waves {
        height: 40px;
        /* Smaller waves for mobile */
        min-height: 40px;
    }

    h1.education {
        font-size: 20px;
        font-family: var(--mainFont);
        /* Adjust font size for mobile */
    }
}

/* Global Styles */
body,
html {
    margin: 0;
    padding: 0;
    font-family: var(--mainFont);
    box-sizing: border-box;
}

/* Container for NavBar */
.container-nav-bar {
    font-family: var(--mainFont);
    width: 100%;
    background: linear-gradient(to right, #212121, #262525);
    color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 10;
}

/* Main NavBar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    flex-wrap: wrap;
}

/* Logo Container */
.logo-container {
    display: flex;
    align-items: center;
}

.lang-bar {
    font-size: 1.4rem;
    margin-right: 1rem;
    cursor: pointer;
    color: #fff;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    z-index: 9999999;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    text-align: center;
    border: 1px solid var(--mainColor);
}

.lang-bar:hover {
    background-color: var(--mainColor);
    transition: ease-in-out all .5s;
    border: 1px solid #252525;
    color: white;
}

.first-logo {
    margin-right: -.22rem;
    transform: scale(5) translate(26%);
}

.title-of-app {
    color: white;
}

.title-app {
    margin: 0;
    font-size: 2.2rem;
}

.title-app span {
    color: var(--mainColor);
}

/* User Logo */
.user-logo {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.user-logo-image img {
    border-radius: 50%;
    margin-right: 0.5rem;
}

.user-name-icon {
    margin: 0;
    font-size: 1.2rem;
}

/* Nav Container */
.nav-container {
    display: flex;
    gap: 1rem;
}

/* Button Styles */
.nav-container .button {
    background-color: var(--mainColor);
    color: white;
    border: 1px solid #000;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    text-decoration: none;
    font-size: 1rem;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
    text-align: center;
    display: inline-block;
}

.nav-container .button:hover {
    background-color: #242424;
    border: 1px solid var(--mainColor);
    color: #fff;
    transform: scale(1.05);
}

.nav-container .button.dropdown {
    background-color: var(--mainColor);
    border: 1px solid #000;
    color: #fff;
    cursor: pointer;
    padding: 0.5rem 1rem;
    position: relative;
}

/* Dropdown Styles */
.dropdown-content,
.dropdown-inscri {
    display: none;
    position: absolute;
    background-color: white;
    min-width: 160px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 20;
    top: 105%;
    left: 49%;
    /* Center alignment */
    transform: translateX(-50%);
    /* Center horizontally */
}

.dropdown-content {
    left: 50%;
    /* Center horizontally */
    transform: translateX(-50%);
    /* Center horizontally */
}

.dropdown-content a,
.dropdown-inscri a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    border-bottom: 2px solid var(--mainColor);
    transition: background-color 0.3s ease;
}

.dropdown-content a:hover,
.dropdown-inscri a:hover {
    background-color: #242424;
    color: white;
}

.button.dropdown:hover .dropdown-content,
.button.dropdown:hover .dropdown-inscri {
    display: block;
}

.arrow-icon {
    margin-left: 0.5rem;
}

/* Container for Slideshow */
.container-slide-show {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    /* background: linear-gradient(135deg, var(--mainColor), #333); */
    /* background-color: #e0e0e0; */
    color: white;
    text-align: center;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

/* Main Image Styles */
.container-slide-show img:first-of-type {
    max-width: 100%;
    animation: fadeInOut 5s infinite;
}

/* Welcome Arrow */
.welcome-arrow {
    position: relative;
    top: 5px;
    /* Adjust as needed to position closer to Commencer button */
    height: 100px;
    /* Adjust for a larger arrow */
    animation: bounce 2s infinite;
}

/* Button Styles */
.start-btn,
.watch-trailer {
    background-color: var(--mainColor);
    color: white;
    border: none;
    border-radius: 5px;
    padding: 1rem 2rem;
    margin: 1rem;
    font-size: 1.5rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
    text-decoration: none;
}

link,
a {
    text-decoration: none;
    color: white;
}

.start-btn:hover,
.watch-trailer:hover {
    background-color: #242424;
    transform: scale(1.05);
    border: 1px solid var(--mainColor);
}

.start-btn span {
    margin-left: 0.5rem;
}

/* Button Container */
.btn-watch-tr {
    display: flex;
    justify-content: center;
}

/* Animations */
@keyframes bounce {

    0%,
    20%,
    50%,
    80%,
    100% {
        transform: translateY(0);
    }

    40% {
        transform: translateY(-15px);
    }

    60% {
        transform: translateY(-7.5px);
    }
}

@keyframes fadeInOut {

    0%,
    100% {
        opacity: .9;
        transform: scale(.9);
    }

    50% {
        opacity: 1;
        transform: scale(1);
    }
}






