/* General Container for Settings */

/* Container for Settings */
.settings-container {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform 0.3s ease;
}

/* Expander Button */
.settings-expander {
    background-color: var(--mainColor);
    border: none;
    border-radius: 50%;
    padding: 0.5rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

.settings-expander:hover {
    background-color: var(--mainColor);
    transform: scale(1.1);
}

.settings-expander img {
    width: 20px;
    height: 20px;
}

/* Settings Panel */
.settings-panel {
    display: none;
    position: absolute;
    bottom: 100%;
    right: 0;
    background-color: #ffffff;
    color: #000000;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 200px;
    transition: opacity 0.3s ease, transform 0.3s ease;
    transform: translateY(10px);
}

.settings-panel.visible {
    display: block;
    opacity: 1;
    transform: translateY(0);
}

/* Header Styles */
.settings-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    border-bottom: 1px solid #e0e0e0;
}

.settings-header h2 {
    margin: 0;
    font-size: 1.2rem;
}

.close-btn {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    transition: color 0.3s ease;
}

.close-btn:hover {
    color: var(--mainColor);
}

/* Content Styles */
.settings-content {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.theme-toggle {
    display: flex;
    align-items: center;
    background-color: #f5f5f5;
    border: none;
    border-radius: 5px;
    padding: 0.5rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.theme-toggle:hover {
    background-color: #e0e0e0;
}

.theme-toggle img {
    width: 30px;
    height: 30px;
    margin-right: 0.5rem;
}

.theme-toggle span {
    font-size: 1rem;
}

/* Dark Mode Styles */
body.dark-mode {
    background-color: #121212 !important;
    color: #519adf !important;
}

body.dark-mode .settings-panel {
    background-color: #333;
    color: #e0e0e0;
}

/* Light Mode Styles */
body.light-mode {
    background-color: #ffffff !important;
    color: #111111 !important;
}

body.light-mode .settings-panel {
    background-color: #ffffff;
    color: #000000;
}



/* ************************************************ */

/* Container for FArticle */
.f-article-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4rem 2rem;
    color: white;
    /* background: linear-gradient(135deg, var(--mainColor), #333); */
    /* background-color: #e0e0e0; */
    position: relative;
    overflow: hidden;
    text-align: center;
}

/* Animated First Image */
.f-animation {
    position: absolute;
    top: 6%;
    left: 71%;
    transform: translateX(-50%);
    animation: float 6s ease-in-out infinite;
    /* z-index: 10 !important; */
}

.f-animation img {
    max-width: 330px;
    height: auto;
}

/* Star Icon */
.f-star {
    margin-top: 4rem;
    /* Increase margin to push content further down */
    animation: pulse 2s infinite;
    z-index: 0;
    /* Ensure the star icon is below the animated vector */
}

/* Question Heading */
.f-question {
    margin-top: 4rem;
    /* Increase margin to push content further down */
    padding: 1rem;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    z-index: 0;
    /* Ensure the question heading is below the animated vector */
}

.question {
    font-size: 2.5rem;
    font-weight: bold;
    color: white;
    /* Ensure text is white for readability */
}

/* Why Container */
.why-container {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
    /* background: linear-gradient(135deg, var(--mainColor), #333); */
    /* background-color: #e0e0e0; */
    margin-top: 4rem;
    /* Increase margin to push content further down */
    z-index: 0;
    /* Ensure the why-container is below the animated vector */
}

.why-container div {
    flex: 1;
    max-width: 300px;
    background: var(--mainColor);
    /* Use the primary color for background */
    color: white;
    /* Ensure text color is white */
    border-radius: 10px;
    padding: 1rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    opacity: 0;
    animation: fadeIn 1s forwards;
    /* Fade-in animation */
}

/* Card Hover Effect */
.why-container div:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

/* Card Title */
.why-container h4 {
    font-size: 1.2rem;
    margin: 0 0 1rem 0;
    color: #e0e0e0;
}

/* Card Image */
.why-container img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
}

/* Animations */
@keyframes float {

    0%,
    100% {
        transform: translateX(-50%) translateY(-10%);
    }

    50% {
        transform: translateX(-50%) translateY(10%);
    }
}

@keyframes pulse {

    0%,
    100% {
        transform: scale(1);
        opacity: 0.8;
    }

    50% {
        transform: scale(1.1);
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}






/* ************************************** */

/* Container for Courses Section */
.courses-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4rem 2rem;
    /* background: linear-gradient(135deg, var(--mainColor), #333); */
    /* background-color: #e0e0e0; */
    /* Light background for the section */
    color: #333;
    /* Dark text color for contrast */
    text-align: center;
    position: relative;
    overflow: hidden;
}

/* Animated Vector */
.animated-vector {
    position: relative;
    margin-bottom: 2rem;
}

.animated-vector img {
    width: 100%;
    height: auto;
    max-height: 320px;
    animation: float 6s ease-in-out infinite;
}

.star {
    position: absolute;
    top: 10%;
    right: 10%;
    width: 50px;
    height: auto;
    animation: spin 4s linear infinite;
}

h5 {
    margin-top: 1rem;
    font-size: 1.5rem;
    font-weight: bold;
    color: #1485bb;
    /* Highlight color for the text */
}

/* Courses Grid */
.courses {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1rem;
    width: 100%;
    max-width: 1200px;
}

/* Course Cards */
.courses div {
    position: relative;
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    padding: 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    color: white;
    text-align: center;
    height: 200px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.courses div:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

/* Background Images */
.courses div:nth-of-type(1) {
    /* background-image: url("../images/econo.jpg"); */
    background-image: url("../images/subjects/eco1.svg");
    background-size: cover;
    background-position: center center;
}

.courses div:nth-of-type(2) {
    /* background-image: url("../images/compta.jpg"); */
    background-image: url("../images/subjects/eco2.svg");
    background-size: cover;
    background-position: center center;
}

.courses div:nth-of-type(3) {
    /* background-image: url("../images/stat.jpg"); */
    background-image: url("../images/subjects/stats.svg");
    background-size: cover;
    background-position: center center;
}

.courses div:nth-of-type(4) {
    /* background-image: url("../images/orga.jpg"); */
    background-image: url("../images/subjects/orga.svg");
    background-size: cover;
    background-position: center center;
}

/* Course Text */
.top {
    font-size: 1.2rem;
    font-weight: bold;
    background-color: var(--mainColor);
    /* Semi-transparent background */
    padding: 0.5rem;
    border-radius: 5px;
    border-top: 3px solid var(--mainColor);
    transform: rotate(90deg) translate(20%, 296%);
}

.bottom {
    font-size: 1.2rem;
    font-weight: bold;
    background-color: var(--mainColor);
    /* Semi-transparent background */
    padding: 0.5rem;
    border-radius: 5px;
    transform: rotate(90deg) translate(20%, 340%);
}

/* Show More Button */
.show-more {
    margin-top: 2rem;
}

.btn-show-more {
    background-color: var(--mainColor);
    color: white;
    border: none;
    border-radius: 5px;
    padding: 1rem 2rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.btn-show-more:hover {
    background-color: var(--mainColor);
    transform: scale(1.05);
}

/* Animations */
@keyframes float {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-10px);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}







