/* ************************************************* */
/* Popup overlay */
.video-popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999999999999999999999999999 !important;
    /* Ensure it is above all other content */
}

/* Popup content */
.video-popup-content {
    position: relative;
    width: 80%;
    max-width: 900px;
    background: #fff;
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    overflow: hidden;
}

/* Close button */
.video-popup-close {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    background: #333;
    color: #fff;
    border: none;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
    cursor: pointer;
    text-align: center;
    line-height: 1.8rem;
    z-index: 10;
}

/* Video styling */
.video-popup-video {
    width: 100%;
    height: auto;
    max-height: 60vh;
    /* Prevent the video from being too tall */
}

/* Button styling */
.btn-show-video {
    background-color: var(--mainColor);
    color: white;
    border: none;
    border-radius: 5px;
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.btn-show-video:hover {
    background-color: var(--mainColor);
    transform: scale(1.05);
}




/* *********************************************** */
/* Footer Container */
.footer-container {
    position: relative;
    bottom: -4%;
    width: 100%;
    /* background: linear-gradient(135deg, var(--mainColor), #333); */
    background-color: #e0e0e0;
    background-color: #333;
    padding: 20px 0;
    color: white;
}

/* Footer */
.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

/* Social Media Icons */
.social-media-icons ul {
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0;
    list-style: none;
    border-radius: 50%;
}

.social-media-icons li {
    margin: 0 10px;
    border-radius: 50%;
}

.social-media-icons img {
    filter: grayscale(100%);
    transition: filter 0.3s, box-shadow 0.3s;
    border-radius: 50%;
}

.social-media-icons a:hover img {
    filter: grayscale(0%);
    box-shadow: 0 4px 8px var(--mainColor);
    cursor: pointer;
}

/* Links */
.link-acces ul {
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 10px 0;
    list-style: none;
}

.link-acces li {
    margin: 0 10px;
}

.link-acces a {
    display: block;
    padding: 10px 20px;
    text-decoration: none;
    background-color: #333;
    color: white;
    border: 2px solid var(--mainColor);
    border-radius: 5px;
    text-align: center;
    transition: background-color 0.3s, border-color 0.3s;
}

.link-acces a:hover {
    background-color: var(--mainColor);
    border-color: black;
}

/* Rights Production */
.rights-production {
    margin-top: 10px;
}


